body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
    width: 100%;
    display: flex;
    height: 30px;
    justify-content: flex-end;
}

.navbar a {
    margin-left: 45px;
    color: #FFFFFF;
    opacity: 0.5;
    font-size: 18px;
    cursor: pointer;
}

.navbar a:link {
    text-decoration: none;
}
.App {
  display: flex;
  min-height: 100vh;
  font-family: 'Arvo', serif;
}

.leftBorder {
  width: 75px;
  background-color: #1D1E20;
}

.pageContainer {
  background-color: #111111;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 60px 100px 60px 45px;
}
.mainContent p {
    color: #FFFFFF;
    opacity: 0.3 !important;
    position: absolute;
    left: 30px;
    font-size: 18px !important;
}

.mainContent {
    color: white;
    font-size: 40px;
}

.name {
    background: linear-gradient(127.76deg, #0DFC9C 0%, #00BDC0 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.mainContent a {
    color: #FFFFFF;
    opacity: 0.5;
    margin: 0 10px;
    cursor: pointer;
    text-decoration: none !important;
}

.indent {
    padding-left: 60px;
    font-size: 30px !important;
}

.line3 {
    color: #FFFFFF;
}

.mainPic {
    border-radius: 50%;
    width: 130px;
    padding: 25px 20px;
}
.workTile {
    height: 450px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 160px;
}

.leftContent {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 60px 0px;
}

.rightContent {
    position: relative;
}

.superTitle {
    font-size: 20px;
}

.title {
    width: 50%;
    font-size: 60px;
}

.built {
    font-size: 12px;
    font-weight: bold;
    opacity: 0.4;
    font-family: 'Roboto', sans-serif !important;
}

.techSection {
    display: flex;
}

.tech {
    margin-right: 20px;
    font-size: 12px;
    font-weight: bold;
    font-family: 'Roboto', sans-serif !important;
}

.box {
    width: 450px;
    height: 450px;
    background-color: #D8D8D8;
}

.button {
    position: absolute;
    bottom: 45px;
    left: -180px;
    width: 200px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(127.76deg, #0DFC9C 0%, #00BDC0 100%);
}

.button a {
    font-size: 18px;
    text-decoration: none;
    color: #FFFFFF
}

.workPage {
    color: #FFFFFF;
    height: 100%;
}

.header {
    margin-top: 30px;
    margin-bottom: 130px;
    font-size: 30px;
}

.footer {
    font-size: 30px;
}
